/* Colors */
:root {
    --PrimaryColor: rgb(255, 109, 56);
    --HoverColor: #2c5873;
    --whiteColor: #fff;
    --blackColor: #000000;
    --textColor: grey;
    --whiteColorDeam: #f0f0f0;
    --greyBg: rgb(225 225 225);
    /* --greyText: rgb(168 168 168); */
    --greyText: #343d53;
    --inputColor: rgb(235 235 235);
    /* --box-shadow: 0 2px 8px 2px rgb(178, 178, 178, 0.189); */
}

.footer {
    background: var(--greyBg);
    padding: 2rem 0 0;

    .secContainer {
        gap: 2rem;
        border-bottom: 1px solid #d2d2d2;
        padding: 0rem 0 2.5rem;

        .footerLogo {
            padding-bottom: 1rem;

            .logo {
                color: var(--blackColor);
                align-items: center;
                font-weight: 500;
                cursor: pointer;

                .icon {
                    color: var(--PrimaryColor);
                    font-size: 35px;
                    margin-right: 5px;
                }
            }
        }

        .socials {
            gap: .5em;
            margin: auto;

            .icon {
                font-size: 30px;
                background: var(--blackColor);
                padding: 8px;
                border-radius: 50%;
                color: var(--whiteColor);
                transition: all .5s ease;

                &:hover {
                    background: var(--PrimaryColor);
                }
            }
        }

        .para {
            padding-bottom: 1rem;
            color: var(--textColor);
            font-size: 14px;
        }

        .footerLinks {
            display: grid;
            row-gap: .2rem;

            .linkTitle {
                padding-bottom: 1rem;
                color: var(--blackColor);
                font-weight: 700;
                font-size: 18px;
            }

            li {
                margin-bottom: 8px;
                transition: all .5s ease;

                &:hover {
                    a {
                        color: var(--PrimaryColor);
                        text-decoration: underline;
                    }
                }
            }

            a,
            .phone,
            .email {
                color: var(--textColor);
                font-size: 14px;
                cursor: pointer;
                padding-bottom: 8px;
                transition: all .5s ease;
            }
        }
    }

    .footerBottom {
        background: var(--greyBg);
        padding-top: 1rem;
        padding-bottom: 1rem;

        p {
            text-align: center;
            font-size: 12px;
            color: var(--textColor);

            a {
                color: var(--textColor);
            }
        }
    }
}

/* MEDIA QUERIES ================== */
@media screen and (max-width: 991px) {
    .footer {
        padding: 3rem 2rem 0;
    }
}


@media screen and (min-width: 448px) {
    .secContainer {
        grid-template-columns: repeat(2, 1fr);
        text-align: start !important;
        align-items: flex-start;

        .footerLinks {
            justify-content: center;
        }
    }
}


@media screen and (min-width: 640px) {
    .secContainer {
        grid-template-columns: repeat(4, 1fr);
    }
}


@media screen and (max-width: 991px) {

    .footer {
        padding: 3rem 2rem 0;

        .secContainer {
            gap: 2rem;
        }
    }
}