/* Colors */
:root {
    --PrimaryColor: rgb(255, 109, 56);
    --HoverColor: #2c5873;
    --whiteColor: #fff;
    --blackColor: #000000;
    --textColor: grey;
    --whiteColorDeam: #f0f0f0;
    --greyBg: rgb(225 225 225);
    /* --greyText: rgb(168 168 168); */
    --greyText: #343d53;
    --inputColor: rgb(235 235 235);
    /* --box-shadow: 0 2px 8px 2px rgb(178, 178, 178, 0.189); */
}

.blog {
    padding: 3rem 0rem 5rem;

    .secContainer {
        margin: 0 1rem;
    }

    .mainContent {
        gap: 2rem;

        .singlePost {
            gap: 2rem;

            .imgDiv {
                width: 100%;
                height: 200px;
                border-radius: 1rem;
                box-shadow: 0 2px 8px 2px rgb(178, 178, 178, 0.406);
                border: 5px solid var(--greyBg);
                overflow: hidden;

                &:hover {
                    img {
                        transform: scale(1.1);
                        transition: .3s ease;
                    }
                }

                img {
                    height: 100%;
                    object-fit: cover;
                }
            }

            .postDetails {
                h3 {
                    color: var(--blackColor);
                    font-weight: 500;
                    padding-bottom: 10px;
                }

                p {
                    color: var(--textColor);
                    font-size: 13px;
                    margin-bottom: 20px;
                }
            }

            a {
                background: var(--PrimaryColor);
                color: var(--whiteColor);
                font-size: 12px;
                width: max-content;
                text-align: center;
                padding: .2rem .3rem .2rem .8rem;
                border-radius: 3rem;
                font-weight: 500;
                transition: all .5s ease;

                &:hover {
                    background: var(--HoverColor);

                    .icon {
                        transform: translateX(3px);
                    }
                }
            }
        }
    }
}

/* MEDIA QUERIES ====================== */
@media screen and (min-width: 884px) {
    .blog {
        .mainContent {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}