@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    transition: .3s ease;
}

/* Colors */
:root {
    --PrimaryColor: rgb(255, 109, 56);
    --HoverColor: #2c5873;
    --whiteColor: #fff;
    --blackColor: #343d53;
    --textColor: grey;
    --whiteColorDeam: #f0f0f0;
    /* --greyBg: rgb(225 225 225); */
    --greyBg: rgb(233, 233, 233);
    /* --greyText: rgb(168 168 168); */
    --greyText: #343d53;
    --inputColor: rgb(207, 207, 207);
    /* --box-shadow: 0 2px 8px 2px rgb(178, 178, 178, 0.334); */
}

/* Default Styling */

a {
    text-decoration: none;
}

li {
    list-style: none;
}

.section {
    padding: 4rem 0 2rem;
}

.container {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}

.icon {
    font-size: 1.5rem;
    cursor: pointer;
}

.flex {
    display: flex;
    align-items: center;
}

.grid {
    display: grid;
    align-items: center;
}

.btn {
    padding: .4rem 1.5rem;
    background: var(--PrimaryColor);
    border: none;
    outline: none;
    border-radius: 3rem;
    cursor: pointer;
}

.btn a {
    color: var(--whiteColor);
    font-weight: 500;
    font-size: 14px;
}

.btn:hover {
    color: var(--HoverColor);
}

.btn:hover a {
    color: var(--whiteColor);
}

.secIntro {
    max-width: 70%;
    text-align: center;
    margin: 0 auto 2rem;
}

.secIntro .secTitle {
    color: var(--blackColor);
    font-weight: 600;
    font-size: 24px;
    margin: 0;
    line-height: 1.42857;
}

.secIntro p {
    font-size: 14px;
    color: var(--textColor);
}

img,
video {
    width: 100%;
    height: auto;
}

/* MEDIA QUERIES ====================== */
@media screen and (min-width: 556px) {

    .mainContent,
    .singlePost {
        grid-template-columns: repeat(2, 1fr);
    }
}


@media screen and (min-width: 884px) {
    .mainContent {
        grid-template-columns: repeat(3, 1fr);
    }
}


@media screen and (min-width: 1240px) {
    .container {
        width: 75% !important;
        margin: auto !important;
    }

    .section {
        padding: 2rem 0.3rem;
    }

    .secIntro {
        margin: 0 auto 3rem;
    }

    .secIntro .secTitle {
        /* font-size: 1.6rem;
        padding-bottom: .5rem;
        line-height: 1.5;
        text-transform: uppercase; */
        font-weight: 600;
        font-size: 30px;
        margin: 0;
        line-height: 1.42857;
    }

    .secIntro p {
        font-size: 15px;
    }
}

@media screen and (min-width: 2560px) {
    body {
        zoom: 1.7;
    }
}