/* Colors */
:root {
    --PrimaryColor: rgb(255, 109, 56);
    --HoverColor: #2c5873;
    --whiteColor: #fff;
    --blackColor: #000000;
    --textColor: grey;
    --whiteColorDeam: #f0f0f0;
    --greyBg: rgb(225 225 225);
    /* --greyText: rgb(168 168 168); */
    --greyText: #343d53;
    --inputColor: rgb(235 235 235);
    /* --box-shadow: 0 2px 8px 2px rgb(178, 178, 178, 0.189); */
}

.about {
    padding: 3rem 0.3rem;

    .secContainer {
        margin: 0 1rem;
        /* .title {
            color: var(--blackColor);
            line-height: 1.5rem;
            text-align: center;
            font-size: 2.2rem;
            font-weight: 800;
            padding-bottom: 1rem;
            line-height: 2rem;
        } */

        .mainContent {
            gap: 1rem;

            .singleItem {
                text-align: center;

                img {
                    background: rgba(247, 144, 54, 0.265);
                    width: 100px;
                    border-radius: 50%;
                    padding: 1rem;
                }

                h3 {
                    font-weight: 700;
                    color: var(--blackColor);
                    padding: .5rem;
                }

                p {
                    font-size: 13px;
                    padding-bottom: .5rem;
                    color: var(--textColor);
                }
            }
        }

        .videoCard {
            margin-top: 2rem;
            border-radius: 10px;
            background: linear-gradient(rgba(0, 0, 0, 0.73), rgba(0, 0, 0, 0.609)), url('../../Assets/map.jpg');
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;

            .cardContent {
                gap: 2rem;
                padding: 2rem 1rem;

                .cardText {
                    h2 {
                        color: var(--whiteColor);
                        font-weight: 600;
                        padding-bottom: 1rem;
                    }

                    p {
                        color: var(--whiteColor);
                        opacity: .7;
                        font-size: 12px;
                        font-weight: 400;
                        letter-spacing: 1px;
                    }
                }
            }

            .cardVideo {
                border: 2px solid var(--whiteColor);
                width: 100%;
                height: 200px;
                border-radius: 10px;
                overflow: hidden;

                video {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}

/* MEDIA QUERIES ====================== */
@media screen and (min-width: 720px) {
    .about {
        .secContainer {
            .videoCard {
                border-radius: 10px;

                .cardContent {
                    gap: 4rem;
                    padding: 4rem 3rem;

                    .cardText {
                        h2 {
                            font-size: 2rem;
                        }

                        p {
                            opacity: .7;
                            font-size: 15px;
                        }
                    }
                }

                .cardContent {
                    grid-template-columns: repeat(2, 1fr) !important;
                }
            }
        }
    }
}

@media screen and (min-width: 1024px) {
    .about {
        .secContainer {
            .mainContent {
                padding: 0rem 0 2.5rem;
            }
        }
    }
}